.products_list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	gap: 20px;
}

.subcategories_list {
	margin-top: 2px;
	padding: 60px 30px 0;
	display: grid;
	grid-template-columns: repeat(3, 310px);
	justify-content: center;
	gap: 20px;
	background-color: var(--white);
}