.about_section {
  background-color: #F2F5F7;
  padding: 80px 0;
}

.about_inner {
  display: flex;
  justify-content: space-around;
  gap: 50px;
}

.about_description {
  max-width: 530px;
}

.about_title {
  font-size: 30px;
  font-weight: 400;
  font-family: 'Prosto One';
  margin-bottom: 24px;
}

.about_text {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 20px;
}

@media (max-width:1060px) {
  .about_inner {
    flex-direction: column;
  }

  .about_description {
    max-width: none;
  }
}